/* Home */
:root {
  --transition: opacity 1100ms cubic-bezier(0.65, 0, 0.35, 1),
    transform 1100ms cubic-bezier(0.65, 0, 0.35, 1);
}

.slider-item {
  pointer-events: none;
}

.slider-item.active {
  pointer-events: auto;
}

.slider-item.active img {
  transition: var(--transition);
  opacity: 1 !important;
}

.slider-item img {
  transition: var(--transition);
  opacity: 0;
}

.slider-item .slider-item__text {
  transition: var(--transition);
  opacity: 0;
}

.slider-item.active .slider-item__text {
  transition: var(--transition);
  opacity: 1;
}

.hero-slider__controller > div.active {
  transition: opacity 450ms cubic-bezier(0.65, 0, 0.35, 1),
    transform 450ms cubic-bezier(0.65, 0, 0.35, 1);
  opacity: 1 !important;
  transform: scale(1.25);
}

.hero-slider__controller > div {
  transition: opacity 450ms cubic-bezier(0.65, 0, 0.35, 1),
    transform 450ms cubic-bezier(0.65, 0, 0.35, 1);
  transform: scale(1);
}

[data-slider="container"] {
  cursor: grab;
}

[data-animation="counter"] {
  font-variant-numeric: tabular-nums;
}

[data-slider="container"]:active {
  cursor: grabbing;
}

.link__underline {
  position: relative;
}

.link__underline::after {
  position: absolute;
  content: "";
  background: #0086bf;
  height: 1.5px;
  width: 100%;
  left: 0;
  bottom: -2px;
  transform-origin: left;
  transform: scaleX(0%);
  transition: cubic-bezier(0.65, 0, 0.35, 1) 325ms;
}

.link__underline:hover .link__underline::after {
  transform: scaleX(100%);
  transition: cubic-bezier(0.65, 0, 0.35, 1) 325ms;
}

.slider-toggle.paused img.pause {
  opacity: 0;
  transition: cubic-bezier(0.65, 0, 0.35, 1) 625ms;
}

.slider-toggle.paused img.play {
  opacity: 1;
  transition: cubic-bezier(0.65, 0, 0.35, 1) 625ms;
}

.slider-toggle img {
  transition: cubic-bezier(0.65, 0, 0.35, 1) 625ms;
}

.slider-toggle img.play {
  opacity: 0;
}

/* Other */
.with-slashes {
  position: relative;
  text-indent: 40px;
}
.with-slashes::before {
  position: absolute;
  left: -40px;
  top: 0;
  font-weight: 900;
  content: "//";
  display: block;
  font-size: inherit;
  font-family: "Swis 721 Extended", sans-serif;
  color: #0086bf;
}

@media screen and (max-width: 991px) {
  .with-slashes {
    text-indent: 30px;
  }
  .with-slashes::before {
    position: absolute;
    left: -26px;
  }
}

.link__underline {
  position: relative;
  transition: cubic-bezier(0.65, 0, 0.35, 1) 250ms;
}

.link__underline::before {
  position: absolute;
  content: "";
  background: #0086bf;
  height: 1.5px;
  width: 100%;
  left: 0;
  bottom: -1px;
  transform-origin: left;
  transform: scaleX(0%);
  transition-delay: 310ms;
  transition: cubic-bezier(0.65, 0, 0.35, 1) 520ms;
}

.link__underline:hover {
  color: #0086bf;
  transition: cubic-bezier(0.65, 0, 0.35, 1) 250ms;
}

.link__underline::after {
  position: absolute;
  content: "";
  background: #0086bf;
  height: 1.5px;
  width: 100%;
  left: 0;
  bottom: -1px;
  transform-origin: right;
  transform: scaleX(100%);
  transition: cubic-bezier(0.65, 0, 0.35, 1) 400ms;
}

.link__underline:hover::after {
  transform: scaleX(0%);
  transition: cubic-bezier(0.65, 0, 0.35, 1) 400ms;
}

.link__underline:hover::before {
  transform: scaleX(100%);
  transition-delay: 310ms;
  transition: cubic-bezier(0.65, 0, 0.35, 1) 520ms;
}

.button__hover {
  transform: scale(1);
  outline: 1.5px solid transparent;
  transition: cubic-bezier(0.65, 0, 0.35, 1) 240ms;
}

.button__hover:hover {
  transform: scale(1.0375);
  background: transparent;
  outline: 1.5px solid #333;
  color: #333 !important;
  transition: cubic-bezier(0.65, 0, 0.35, 1) 240ms;
}

.hero-slider .button__hover:hover {
  background: #333;
  transform: scale(1) !important;
  color: white !important;
  outline: none !important;
  transition: cubic-bezier(0.65, 0, 0.35, 1) 240ms;
}

/* Lenis */
html.lenis,
html.lenis body {
  height: auto;
}

.lenis.lenis-smooth {
  scroll-behavior: auto !important;
}

.lenis.lenis-smooth [data-lenis-prevent] {
  overscroll-behavior: contain;
}

.lenis.lenis-stopped {
  overflow: hidden;
}

.lenis.lenis-scrolling iframe {
  pointer-events: none;
}

/* project */
.cant-scroll {
  opacity: 0;
  transition: 300ms all ease-out;
}

.cant-scroll {
  opacity: 0;
  transition: 300ms all ease-out;
  pointer-events: none;
}

.is-left {
  transform: translateX(calc(-100% - 32px)) translateY(20px) !important;
}
