[data-point="point"][data-open="true"] [data-point="marker"] > div {
  background: var(--archallblue) !important;
}

[data-point="marker"] > div {
  transition: 150ms ease-out all;
}

/* Sector */
/* Only on desktop */
@media screen and (min-width: 768px) {
  .sector-project:nth-child(3n + 1) {
      /* width: 68%; */
      flex-basis: calc((100% - 64px)/(3/2));
      flex-grow: 1;
  }

  .sector-project:nth-child(3n + 4) {
      /* width: 68%; */
      flex-basis: calc((100% - 64px)/(3/2));
      flex-grow: 1;
  }

  /* Small ones */
  .sector-project:nth-child(5n - 3) {
      /* width: 30%; */
      flex-basis: 30%;
      flex-grow: 1;
  }
}

